import React from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";

import { List, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import { Chip, Drawer, Divider, IconButton } from "@material-ui/core";

import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import FormatQuoteIcon from "@material-ui/icons/FormatQuote";
import SchoolIcon from "@material-ui/icons/School";
import EmojiEventsIcon from "@material-ui/icons/EmojiEvents";
import WorkIcon from "@material-ui/icons/Work";
import BusinessCenterIcon from "@material-ui/icons/BusinessCenter";
import LaptopMacIcon from "@material-ui/icons/LaptopMac";
import MailIcon from "@material-ui/icons/Mail";

import SideImage from "../components/side-image";

const drawerWidth = 260;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: -3,
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("md")]: {
      width: theme.spacing(9) + 1,
    },
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
  },
  title: {
    position: "absolute",
    left: "10px",
    color: "white",
    padding: "2px 5px",
    [theme.breakpoints.down("md")]: {
      fontSize: 15,
    },
    [theme.breakpoints.up("md")]: {
      fontSize: 18,
    },
    backgroundColor: "green",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

export default function SideDrawer({ tabId, handleTabChange }) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const myIcons = {
    "About ": <FormatQuoteIcon id={0} />,
    "Education ": <SchoolIcon id={1} />,
    "Awards and Certificates": <EmojiEventsIcon id={2} />,
    "Experience ": <WorkIcon id={3} />,
    "Skills ": <BusinessCenterIcon id={4} />,
    "Projects ": <LaptopMacIcon id={5} />,
    "Contact ": <MailIcon id={6} />,
  };

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.root}>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <div className={classes.toolbar}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
            <MenuIcon />
          </IconButton>
          <Chip
            className={clsx(classes.title, {
              [classes.hide]: !open,
            })}
            label="Soheil Changizi"
          />
          <IconButton
            onClick={handleDrawerClose}
            className={clsx(classes.menuButton, {
              [classes.hide]: !open,
            })}
          >
            <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider />

        <List
          onClick={(event) => {
            handleTabChange(event);
            handleDrawerClose();
          }}
        >
          <SideImage small={!open} hideName />
          {Object.keys(myIcons).map((label, index) => (
            <ListItem id={index} button key={label}>
              <ListItemIcon id={index}>
                {React.cloneElement(myIcons[label], {
                  color:
                    tabId === myIcons[label].props.id ? "secondary" : "default",
                })}
              </ListItemIcon>
              <ListItemText id={index} primary={label} />
            </ListItem>
          ))}
        </List>
      </Drawer>
    </div>
  );
}
