import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Chip from "@material-ui/core/Chip";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";

import ComputerIcon from "@material-ui/icons/Computer";
import LocationOnIcon from "@material-ui/icons/LocationOn";

import ProfileImage from "../assets/about-min.jpg";

const useStyles = makeStyles((theme) => ({
  root: {
    position: "relative",
  },
  body1: {
    [theme.breakpoints.up("md")]: {
      marginTop: 0,
    },
  },
  body2: {
    [theme.breakpoints.up("md")]: {
      marginTop: 10,
      marginBottom: -5,
    },
  },
  overlay: {
    position: "absolute",
    bottom: "100px",
    left: "10px",
    color: "white",
    padding: "2px 5px",
    [theme.breakpoints.down("md")]: {
      fontSize: 15,
    },
    [theme.breakpoints.up("md")]: {
      fontSize: 18,
    },
    backgroundColor: "green",
  },
  icon: {
    [theme.breakpoints.down("md")]: {
      paddingTop: 10,
    },
    [theme.breakpoints.up("md")]: {
      marginTop: -5,
      marginBottom: -4,
      paddingTop: 5,
    },
    color: "green",
  },
  image: {
    textIndent: "-9999px",
  },
}));

export default function SideImage({ small, hideName }) {
  const classes = useStyles();

  return (
    <Card className={classes.root}>
      <CardMedia
        component="img"
        src={ProfileImage}
        alt="profile"
        width="100%"
        title="Profile Picture"
        className={classes.image}
      />
      {!small && (
        <>
          {!hideName && (
            <Chip className={classes.overlay} label="Soheil Changizi" />
          )}
          <CardContent>
            <Typography
              className={classes.body1}
              variant="body2"
              color="textSecondary"
              component="p"
            >
              <ComputerIcon className={classes.icon} /> Computer Scientist
            </Typography>
            <Typography
              className={classes.body2}
              variant="body2"
              color="textSecondary"
              component="p"
            >
              <LocationOnIcon className={classes.icon} /> Winnipeg, Canada
            </Typography>
          </CardContent>
        </>
      )}
    </Card>
  );
}
